import { useState, useRef, useEffect } from "react";

import React from "react";
import axios from "axios";
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import "./AudioRecorder.css";

axios.defaults.xsrfCookieName = "csrftoken";
axios.defaults.xsrfHeaderName = "X-CSRFToken";

const mimeType = "audio/webm";

const AudioRecorder = () => {
  console.log("In AudioRecorder");

  const [permission, setPermission] = useState(false);
  const [stream, setStream] = useState(null);
  const mediaRecorder = useRef(null);
  const [recordingStatus, setRecordingStatus] = useState("inactive");
  const [audioChunks, setAudioChunks] = useState([]);
  const [audioBlob, setAudioBlob] = useState(null);
  const [audioBlobUrl, setAudioBlobUrl] = useState(null);

  const [blobSubmitStatus, setBlobSubmitStatus] = useState(0);
  const [blobAsrTranscript, setBlobAsrTranscript] = useState("");
  const [blobAsrTranscriptStatus, setBlobAsrTranscriptStatus] = useState(false);

  const [blobUploadStatus, setBlobUploadStatus] = useState(0);

  const getMicrophonePermission = async () => {
    if ("MediaRecorder" in window) {
      try {
        const streamData = await navigator.mediaDevices.getUserMedia({
          audio: true,
          video: false,
        });
        setPermission(true);
        setStream(streamData);
      } catch (err) {
        alert(err.message);
      }
    } else {
      alert("The MediaRecorder API is not supported in your browser.");
    }
  };

  const startRecording = async () => {
    setRecordingStatus("recording");
    //create new Media recorder instance using the stream
    const media = new MediaRecorder(stream, { type: mimeType });
    //set the MediaRecorder instance to the mediaRecorder ref
    mediaRecorder.current = media;
    //invokes the start method to start the recording process
    mediaRecorder.current.start();
    let localAudioChunks = [];
    mediaRecorder.current.ondataavailable = (event) => {
      if (typeof event.data === "undefined") return;
      if (event.data.size === 0) return;
      localAudioChunks.push(event.data);
    };
    setAudioChunks(localAudioChunks);
  };

  const stopRecording = () => {
    setRecordingStatus("inactive");
    //stops the recording instance
    mediaRecorder.current.stop();
    mediaRecorder.current.onstop = () => {
      //creates a blob file from the audiochunks data
      const audioBlob = new Blob(audioChunks, { type: mimeType });
      //creates a playable URL from the blob file.
      const audioUrl = URL.createObjectURL(audioBlob);
      setAudioBlob(audioBlob);
      setAudioBlobUrl(audioUrl);
      setAudioChunks([]);
    };
  };

  const handleSubmit1 = (event) => {
    event.preventDefault();
    setBlobSubmitStatus(blobSubmitStatus + 1);
    console.log("In handleSubmit1");
    var elementPop = document.getElementById("popDivASRR");
    if (elementPop) {
      var elementPopParent = elementPop.parentElement;
      elementPopParent.removeChild(elementPop);
    }

    var dialogBoxElement = document.createElement("div");
    dialogBoxElement.setAttribute("id", "popDivASRR");
    dialogBoxElement.setAttribute("class", "ontop");

    var dialogBoxElementInner = document.createElement("div");
    dialogBoxElementInner.setAttribute("id", "popupASRR");
    dialogBoxElementInner.innerHTML = "Loading";
    dialogBoxElementInner.style.textAlign = "center";
    dialogBoxElementInner.style.fontSize = "100px";
    dialogBoxElementInner.style.position = "absolute";
    dialogBoxElementInner.style.top = "300px";
    dialogBoxElementInner.style.left = "700px";

    dialogBoxElement.appendChild(dialogBoxElementInner);

    document.getElementsByTagName("body")[0].appendChild(dialogBoxElement);
    dialogBoxElement.style.display = "block";
  };

  useEffect(() => {
    const putBlobinBackend = async () => {
      try {
        const formData = new FormData();

        const headers1 = {
          "Content-Type": "multipart/form-data",
        };

        //const fileInput = document.getElementById("myaudioBlob").src;
        //const audioFile = fileInput.files[0];

        formData.append("myblob", audioBlob);

        console.log(formData);
        console.log(audioBlob);
        try {
          const response = await axios.post(
            "sanskrit-gallery/granthapp/api/blobUploadASR/",
            formData
          );

          console.log(response);
        } catch (error) {
          console.log(error);
        }
        setBlobUploadStatus(blobUploadStatus + 1);
      } catch (err) {
        //alert(err);
      }
    };

    async function callerBlobFun() {
      console.log("callerBlobFun");
      console.log("Before putBlobinBackend blobSubmitStatus", blobSubmitStatus);
      await putBlobinBackend();
      console.log("Caller");
    }

    if (blobSubmitStatus > 0) {
      callerBlobFun();
    }
  }, [blobSubmitStatus]);

  useEffect(() => {
    const getBlobinBackend = async () => {
      try {
        const temp = await axios.get("sankrit-gallery/granthapp/api/ASRTranscriptGet/");
        console.log("temp ", temp);
        console.log("temp.data ", temp.data);

        console.log("temp.data.transcript ", temp.data.transcript);

        setBlobAsrTranscript(temp.data.transcript);
        console.log(
          "blobAsrTranscript",
          blobAsrTranscript,
          blobAsrTranscript.length
        );
        setBlobAsrTranscriptStatus(true);
      } catch (err) {
        console.log(err);
      }
    };

    async function callerBlobFun1() {
      console.log("callerBlobFun1");
      console.log("Before getBlobinBackend blobUploadStatus", blobUploadStatus);
      await getBlobinBackend();
      console.log("Caller");
    }
    if (blobUploadStatus > 0) {
      callerBlobFun1();
      var elementPop = document.getElementById("popDivASRR");
      elementPop.style.display = "none";
    }
  }, [blobUploadStatus]);

  return (
    <div>
      {/*<label className="form-label">Record Audio</label>*/}
      <main>
        <div className="audio-controls">
          {!permission ? (
            <button
              onClick={getMicrophonePermission}
              type="button"
              className="btn btn-secondary"
              style={{ marginTop: "10px" }}
            >
              Get Microphone
            </button>
          ) : null}

          {permission && recordingStatus === "inactive" ? (
            <button
              onClick={startRecording}
              type="button"
              className="btn btn-secondary"
              style={{ marginTop: "10px" }}
            >
              Start Recording
            </button>
          ) : null}
          {recordingStatus === "recording" ? (
            <button
              onClick={stopRecording}
              type="button"
              className="btn btn-secondary"
              style={{ marginTop: "10px" }}
            >
              Stop Recording
            </button>
          ) : null}

          {audioBlob ? (
            <div className="audio-container">
              <audio id="myaudioBlob" src={audioBlobUrl} controls></audio>
              <a download href={audioBlobUrl}>
                Download Recording
              </a>
            </div>
          ) : null}
          {audioBlob ? (
            <button
              onClick={handleSubmit1}
              type="button"
              className="btn btn-secondary"
              style={{ marginTop: "10px" }}
            >
              Submit
            </button>
          ) : null}
        </div>
        {blobAsrTranscriptStatus ? (
          <div
            style={{
              minHeight: "100px",
              border: "1px solid rgba(128, 128, 128, 0.25)",
            }}
          >
            {" "}
            {blobAsrTranscript}
          </div>
        ) : (
          <></>
        )}
      </main>
    </div>
  );
};
export default AudioRecorder;
