import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import "./Lexicon.css";

const Lexicon = () => {
  const [files, setFiles] = useState([]);
  const [lexiconWordList, setLexiconWordList] = useState([]);
  const [getTextsubmitStatus, setGetTextsubmitStatus] = useState(-1);
  const [getTextsubmitStatusIntFlag, setGetTextsubmitStatusIntFlag] =
    useState(0);
  const [lexiconWordListIntFlag, setLexiconWordListIntFlag] = useState(0);

  const onSubmitHandler = (e) => {
    e.preventDefault();
    let response;
    const getTexts = async () => {
      try {
        response = await axios.get("/sanskrit-gallery/granthapp/api/loadLexicon/");
        console.log("datann", response.data["fileList"]);
      } catch (error) {
        console.log(error);
      }

      if (response.data.length !== 0) {
        setFiles(response.data["fileList"]);
      } else {
        setFiles([]);
      }
    };
    getTexts();
  };

  useEffect(() => {
    document.getElementById("buttonce").click();
  }, []);

  function hideDialog() {
    document.getElementById("popDivLexicon").style.display = "none";
  }

  const handleClick = (e) => {
    console.log("e", e);
    e.preventDefault();

    const id = parseInt(e.target.getAttribute("id"));
    console.log("id", id, typeof id);
    setGetTextsubmitStatus(id);
    setGetTextsubmitStatusIntFlag(getTextsubmitStatusIntFlag + 1);
  };

  useEffect(() => {
    const getText = async () => {
      let response1;

      try {
        response1 = await axios.get(
          "/sanskrit-gallery/granthapp/api/loadLexiconText/" + files[getTextsubmitStatus]
        );
        console.log("datann", response1.data["lexiconWordList"]);
        if (response1.data.length !== 0) {
          setLexiconWordList(response1.data["lexiconWordList"]);
          setLexiconWordListIntFlag(lexiconWordListIntFlag + 1);
        } else {
          setLexiconWordList([]);
        }
      } catch (error) {
        console.log(error);
      }
    };

    async function callerFun() {
      console.log("callerFun");
      console.log("Before getText ");
      await getText();
      console.log("Caller");
    }
    if (getTextsubmitStatus !== -1) {
      callerFun();
    }
  }, [getTextsubmitStatusIntFlag]);

  useEffect(() => {
    if (lexiconWordList.length > 0) {
      var elementPop = document.getElementById("popDivLexicon");
      if (elementPop) {
        var elementPopParent = elementPop.parentElement;
        elementPopParent.removeChild(elementPop);
      }

      var dialogBoxElement = document.createElement("div");
      dialogBoxElement.setAttribute("id", "popDivLexicon");
      dialogBoxElement.setAttribute("class", "ontop");

      var dialogBoxElementInner = document.createElement("div");
      dialogBoxElementInner.setAttribute("id", "popupLexicon");
      console.log(
        "typeof(lexiconWordList)",
        typeof lexiconWordList,
        lexiconWordList
      );

      var tableDialogBoxElement = document.createElement("table");
      tableDialogBoxElement.setAttribute("class", "sk-font-Sh");

      var tr = document.createElement("tr");
      var td = document.createElement("td");
      td.innerHTML = "#";
      td.style.color = "black";
      td.style.fontSize = "18px";
      td.style.textAlign = "center";
      td.colSpan = "8";
      td.style.fontWeight = "bold";
      tr.appendChild(td);
      var td1 = document.createElement("td");
      td1.innerHTML = "     Total (" + lexiconWordList.length + ")";
      td1.style.color = "black";
      td1.style.fontSize = "18px";
      td1.style.textAlign = "center";
      td1.colSpan = "5";
      td1.style.fontWeight = "bold";
      tr.style.borderBottom = "1px solid #80808040";
      tr.style.paddding = "1px";
      tr.appendChild(td1);
      tableDialogBoxElement.appendChild(tr);
      tr.style.borderBottom = "1px solid #80808040";
      tr.style.paddding = "1px";
      tableDialogBoxElement.appendChild(tr);

      for (let i = 0; i < lexiconWordList.length; i++) {
        var tr0 = document.createElement("tr");
        tr0.style.borderBottom = "1px solid #80808040";
        tr0.style.paddding = "1px";
        var td0 = document.createElement("td");
        td0.innerHTML = i + 1;
        td0.style.color = "black";
        td0.style.fontSize = "18px";
        td0.style.textAlign = "center";
        td0.colSpan = "8";
        td0.style.fontWeight = "none";
        tr0.appendChild(td0);
        var td01 = document.createElement("td");

        td01.innerHTML =
          "&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;" +
          lexiconWordList[i];
        td01.style.color = "black";
        td01.style.fontSize = "18px";
        td01.style.textAlign = "left";
        td01.colSpan = "5";
        td01.style.fontWeight = "none";
        td01.style.marginLeft = "10px";
        tr0.appendChild(td01);
        tableDialogBoxElement.appendChild(tr0);
      }
      //dialogBoxElementInner.setAttribute("class", "ontopInner");
      dialogBoxElementInner.appendChild(tableDialogBoxElement);

      dialogBoxElement.appendChild(dialogBoxElementInner);

      document.getElementsByTagName("body")[0].appendChild(dialogBoxElement);

      var buttonDialogBoxElement1 = document.createElement("button");
      buttonDialogBoxElement1.setAttribute("class", "btn");
      buttonDialogBoxElement1.classList.add("btn-dark");
      buttonDialogBoxElement1.classList.add("active");
      buttonDialogBoxElement1.innerHTML = "Cancel";
      buttonDialogBoxElement1.style.position = "absolute";
      buttonDialogBoxElement1.style.top = "885px";
      buttonDialogBoxElement1.style.left = "970px";

      dialogBoxElement.appendChild(buttonDialogBoxElement1);

      if (lexiconWordList.length > 0) {
        dialogBoxElement.style.display = "block";
      }

      buttonDialogBoxElement1.addEventListener("click", function (e) {
        hideDialog();
      });
    }
  }, [lexiconWordList]);

  const printResults = () => {
    if (files.length === 0) {
      return <p>No lexicon</p>;
    } else {
      return (
        <table className="table ">
          <thead className="thead-light">
            <tr>
              <th> # </th>
              <th>
                {" "}
                Filename <i> (Click on the below names to view the lexicon)</i>
              </th>
            </tr>
          </thead>
          <tbody>
            {files.map((item, index) => {
              return (
                <tr key={index}>
                  <td>{index + 1}</td>
                  <td key={index}>
                    <p
                      id={index}
                      onClick={handleClick}
                      style={{ cursor: "pointer" }}
                    >
                      {item}
                    </p>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      );
    }
  };

  return (
    <>
      <Container className="mt-5">
        <Button
          id="buttonce"
          variant="outline-success"
          type="submit"
          onClick={onSubmitHandler}
          style={{ backgroundColor: "rgb(239, 231, 214)", display: "none" }}
        >
          Load Lexicon
        </Button>
      </Container>

      <Container className="mt-3">{printResults()}</Container>
    </>
  );
};

export default Lexicon;
