  function rangeEndFix() {
    var jRange = null;
    var selectedMode1Range = null;

    if (document.getElementById("moolaLoopSelect")) {
      jRange = document.getElementById("moolaLoopSelect").selectedIndex;
      selectedMode1Range =
        document.getElementById("moolaLoopSelect").options[jRange].text;

      console.log("selectedMode1Range", selectedMode1Range);

      var rangeEndCount = 0;

      var i;

      if (selectedMode1Range === "") {
        var collPElement = document
          .getElementById("divContainerElement")
          .getElementsByTagName("p");

        console.log("collPElement", collPElement);

        for (i = 0; i < collPElement.length; i++) {
          if (!collPElement[i].getAttribute("id")) {
            continue;
          }
          console.log("collPElement[i]", collPElement[i]);

          if (collPElement[i].getAttribute("id").split(":")[0] === "SC3") {
            rangeEndCount = rangeEndCount + 1;
          }
        }
      } else {
        var collSpanElement = document
          .getElementById("divContainerElement")
          .getElementsByTagName("span");
        console.log("collSpanElement", collSpanElement);

        var previousAudiomapFix = 0;

        for (i = 0; i < collSpanElement.length; i++) {
          if (!collSpanElement[i].getAttribute("audiomap")) {
            continue;
          }
          if (
            collSpanElement[i].getAttribute("audiomap").split(".").length === 1
          ) {
            if (
              parseInt(collSpanElement[i].getAttribute("audiomap")) ===
              previousAudiomapFix
            ) {
            } else {
              rangeEndCount = rangeEndCount + 1;
              previousAudiomapFix = parseInt(
                collSpanElement[i].getAttribute("audiomap")
              );
            }
          }
        }
      }

      console.log("rangeStartB", rangeEndCount);
      document.getElementById("rangeStartA").setAttribute("value", 1);

      document
        .getElementById("rangeStartB")
        .setAttribute("value", rangeEndCount);

      var rMode = "Repeat Each ";
      rMode = rMode + selectedMode1Range + ":";

      document.getElementById("rMode").innerHTML = rMode;
    }
  }

  const buttonOpr = () => {
    var coll = document.getElementsByClassName("collapsible");
    var i;
    //console.log("coll", coll);
    for (i = 0; i < coll.length; i++) {
      coll[i].addEventListener("click", function () {
        var content = this.parentElement.nextElementSibling;
        //console.log("content", content);
        while (content !== null) {
          //console.log("content", content);
          if (content.style.display === "block") {
            content.style.display = "none";
            this.innerHTML = "+";
          } else {
            content.style.display = "block";
            this.innerHTML = "-";
          }
          content = content.nextElementSibling;
          //console.log("content after", content);
        }
      });
    }
  };

  const buttonPrune = () => {
    var coll = document.getElementsByClassName("collapsible");
    var i;
    //console.log("coll", coll);
    for (i = 0; i < coll.length; i++) {
      var content = coll[i].parentElement.nextElementSibling;
      //console.log("content, buttonPrune", content);
      if (content === null) {
        //console.log("content, buttonPrune", content);
        var contentParent = coll[i].parentElement;
        //console.log("contentParent", contentParent);
        contentParent.removeChild(contentParent.lastChild);
        i = i - 1;
      }
    }
  };

