  async function textSeek(e) {
    //alert("wait");
    //console.log("eeeeeeeeeeeeeeeeee", e);
    //audioPlayerRef.current.currentTime = e.target.getAttribute("begin");
    console.log("textSeek previousAudioPlaying", previousAudioPlaying);
    //alert("wait");
    /*console.log(
      "e.target.getAttribute(audiomap)",
      e.target.getAttribute("audiomap")
    );*/
    //alert("wait");

    if (previousAudioPlaying) {
      //console.log("previousAudioPlaying", previousAudioPlaying);
      //alert("wait");
      previousAudioPlaying.pause();
    }
    //alert("wait");

    let audiomapID = e.target.getAttribute("audiomap");
    let audiomapIDArray = audiomapID.split(".");

    //console.log("audiomapIDArray", audiomapIDArray);

    var audio = null;

    if (
      document.getElementById("moolaLoop").getAttribute("loopStatus") ===
        "LOOP ON" ||
      repeatFlag
    ) {
      previousAudioPlaying.play();
    } else {
      if (audiomapIDArray.length === 1) {
        audio = document.getElementById("moolaAudio");
      } else {
        audio = document.getElementById(
          e.target.getAttribute("audiomap") + "audio"
        );
      }
      //alert("wait");
      //console.log("audio", audio);
      if (audio) {
        if (!isNaN(parseFloat(e.target.getAttribute("begin")))) {
          if (audiomapIDArray.length === 1) {
            if (
              document
                .getElementById("moolaLoop")
                .getAttribute("loopStatus") === "LOOP ON" ||
              repeatFlag
            ) {
            } else {
              audio.currentTime = parseFloat(e.target.getAttribute("begin"));
            }
          } else {
            audio.currentTime = parseFloat(e.target.getAttribute("begin"));
          }
          audio.play(); //console.log("ele.current", ele.current);
          previousAudioPlaying = audio;
        }
      }
    }
  }

  async function textSeekHeading(e) {
    console.log("textSeekHeading previousAudioPlaying", previousAudioPlaying);

    if (previousAudioPlaying) {
      previousAudioPlaying.pause();
    }

    let clickedElement = e.target;
    console.log("clickedElement", clickedElement, typeof clickedElement);

    clickedElement = clickedElement.parentElement;

    while (clickedElement.nodeName !== "DIV") {
      console.log("clickedElement", clickedElement, typeof clickedElement);
      clickedElement = clickedElement.nextElementSibling;
    }
    const clickedElementChildNodes = clickedElement.childNodes;
    //console.log("mainDivChildNodes", mainDivChildNodes);
    for (let i = 0; i < clickedElementChildNodes.length; i++) {
      //console.log(mainDivChildNodes[i]);
      if (clickedElementChildNodes[i].nodeName === "P") {
        clickedElement = clickedElementChildNodes[i].childNodes[0];
        break;
      }
    }

    let audiomapID = clickedElement.getAttribute("audiomap");
    let audiomapIDArray = audiomapID.split(".");

    //console.log("audiomapIDArray", audiomapIDArray);

    var audio = null;

    if (audiomapIDArray.length === 1) {
      audio = document.getElementById("moolaAudio");
    }

    //console.log("audio", audio);
    if (audio) {
      if (
        document.getElementById("moolaLoop").getAttribute("loopStatus") ===
          "LOOP ON" ||
        repeatFlag
      ) {
      } else {
        audio.currentTime = parseFloat(clickedElement.getAttribute("begin"));
      }
      audio.play(); //console.log("ele.current", ele.current);
      previousAudioPlaying = audio;
    }
  }

