import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";

axios.defaults.xsrfCookieName = "csrftoken";
axios.defaults.xsrfHeaderName = "X-CSRFToken";

const TTS = () => {
  const [submitStatus, setSubmitStatus] = useState(0);
  const [uploadStatus, setUploadStatus] = useState(0);
  const [Transcript, setTranscript] = useState("");
  const [audioFile, setAudioFile] = useState(null);
  const [audioFileFlag, setAudioFileFlag] = useState(false);

  const handleChange = (event) => {
    setTranscript(event.target.value);
    console.log(event.target.value);
    console.log("Transcript", Transcript, typeof Transcript);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setSubmitStatus(submitStatus + 1);
  };

  useEffect(() => {
    const putFileinBackend = async () => {
      try {
        const formData = new FormData();
        formData.append("myFile", Transcript);

        console.log(formData);
        console.log("Transcript", Transcript, typeof Transcript);
        try {
          const response = await axios.post(
            "/sanskrit-gallery/granthapp/api/textUploadTTS/",
            formData
          );

          console.log(response);
        } catch (error) {
          console.log(error);
        }
        setUploadStatus(uploadStatus + 1);
      } catch (err) {
        alert(err);
      }
    };

    async function callerFun() {
      console.log("callerFun");
      console.log("Before putFileinBackend submitStatus", submitStatus);
      await putFileinBackend();
      console.log("Caller");
    }

    callerFun();
  }, [submitStatus]);

  useEffect(() => {
    const getFileinBackend = async () => {
      try {
        const temp = await axios.get("/sanskrit-gallery/granthapp/api/TTSAudioGet/");
        console.log("temp", temp);
        //const blob = new Blob([temp.data], { type: "audio/mp3" });
        //const url = URL.createObjectURL(blob);
        setAudioFile(temp.data["fname"]);
        setAudioFileFlag(true);
      } catch (err) {
        console.log(err);
      }
    };

    async function callerFun1() {
      console.log("callerFun");
      console.log("Before putFileinBackend submitStatus", uploadStatus);
      await getFileinBackend();
      console.log("Caller");
    }

    callerFun1();
  }, [uploadStatus]);

  return (
    <>
      <Container className="mt-5">
        <div>
          <p>Upload Text</p>
          <label>Enter value : </label>
          <textarea value={Transcript} onChange={handleChange} />

          <Button variant="primary" onClick={handleSubmit}>
            Submit
          </Button>
        </div>
        {console.log("audioFileFlag", audioFileFlag)}
        {audioFileFlag ? (
          <audio controls>
            <source src={audioFile} type="audio/mp3" />
          </audio>
        ) : (
          <></>
        )}
      </Container>
    </>
  );
};

export default TTS;
