import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import AudioRecorder from "../pages/AudioRecorder";
import "./ASR.css";

axios.defaults.xsrfCookieName = "csrftoken";
axios.defaults.xsrfHeaderName = "X-CSRFToken";

const ASR = () => {
  const [audioFile, setAudioFile] = useState(null);
  const [submitStatus, setSubmitStatus] = useState(0);
  const [uploadStatus, setUploadStatus] = useState(0);
  const [asrTranscript, setAsrTranscript] = useState("");
  const [asrTranscriptStatus, setAsrTranscriptStatus] = useState(false);

  const [clickAudioUploadOption, setClickAudioUploadOption] = useState(true);

  const [rkeyAudio, setRkeyAudio] = useState(1);

  const handleAudioUpload = (event) => {
    setAudioFile(event.target.files[0]);
    console.log("audioFile");
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setSubmitStatus(submitStatus + 1);
    var elementPop = document.getElementById("popDivASRUA");
    if (elementPop) {
      var elementPopParent = elementPop.parentElement;
      elementPopParent.removeChild(elementPop);
    }

    var dialogBoxElement = document.createElement("div");
    dialogBoxElement.setAttribute("id", "popDivASRUA");
    dialogBoxElement.setAttribute("class", "ontop");

    var dialogBoxElementInner = document.createElement("div");
    dialogBoxElementInner.setAttribute("id", "popupASRUA");
    dialogBoxElementInner.innerHTML = "Loading";
    dialogBoxElementInner.style.textAlign = "center";
    dialogBoxElementInner.style.fontSize = "100px";
    dialogBoxElementInner.style.position = "absolute";
    dialogBoxElementInner.style.top = "300px";
    dialogBoxElementInner.style.left = "700px";

    dialogBoxElement.appendChild(dialogBoxElementInner);

    document.getElementsByTagName("body")[0].appendChild(dialogBoxElement);
    dialogBoxElement.style.display = "block";
  };

  useEffect(() => {
    const putFileinBackend = async () => {
      try {
        const formData = new FormData();

        formData.append("myFile", audioFile);

        console.log(formData);
        console.log(audioFile);
        try {
          const response = await axios.post(
            "/sanskrit-gallery/granthapp/api/fileUploadASR/",
            formData
          );

          console.log("Inside putFileinBackend", response);
        } catch (error) {
          console.log(error);
        }
        setUploadStatus(uploadStatus + 1);
      } catch (err) {
        alert(err);
      }
    };

    async function callerFun() {
      console.log("callerFun");
      console.log("Before putFileinBackend submitStatus", submitStatus);
      await putFileinBackend();
      console.log("Caller");
    }

    if (submitStatus !== 0) {
      console.log("submitStatus", submitStatus);
      callerFun();
    }
  }, [submitStatus]);

  useEffect(() => {
    const getFileinBackend = async () => {
      try {
        const temp = await axios.get("/sanskrit-gallery/granthapp/api/ASRTranscriptGet/");
        setAsrTranscript(temp.data.transcript);
        console.log("asrTranscript", asrTranscript, asrTranscript.length);
        setAsrTranscriptStatus(true);
      } catch (err) {
        console.log(err);
      }
    };

    async function callerFun1() {
      console.log("callerFun1");
      console.log("Before getFileinBackend uploadStatus", uploadStatus);
      await getFileinBackend();
      console.log("Caller1");
    }

    if (uploadStatus !== 0) {
      console.log("uploadStatus", uploadStatus);
      callerFun1();
      var elementPop = document.getElementById("popDivASRUA");
      elementPop.style.display = "none";
    }
  }, [uploadStatus]);

  const clickAudioUpload = (event) => {
    event.preventDefault();
    setClickAudioUploadOption(true);
    setAsrTranscriptStatus(false);
    setAudioFile(null);
  };

  const clickRecord = (event) => {
    event.preventDefault();
    setClickAudioUploadOption(false);
    setAsrTranscriptStatus(false);
    setRkeyAudio(rkeyAudio + 1);
  };

  return (
    <>
      <Container className="mt-5">
        <Button
          variant="outline-success"
          type="button"
          onClick={clickAudioUpload}
          style={{ backgroundColor: "rgb(239, 231, 214)" }}
        >
          Upload Audio
        </Button>
        &nbsp;&nbsp;&nbsp;
        <Button
          variant="outline-success"
          type="button"
          onClick={clickRecord}
          style={{ backgroundColor: "rgb(239, 231, 214)" }}
        >
          Record from browser mic
        </Button>
        <br></br>
        <br></br>
        {clickAudioUploadOption ? (
          <Form onSubmit={handleSubmit}>
            <Form.Group controlId="audioForm">
              <Form.Control
                type="file"
                accept=".mp3,.wav"
                onChange={handleAudioUpload}
              />
            </Form.Group>
            {audioFile !== null ? (
              <button
                type="submit"
                className="btn btn-secondary"
                style={{ marginTop: "10px" }}
              >
                Submit
              </button>
            ) : (
              <></>
            )}
          </Form>
        ) : (
          <AudioRecorder key={rkeyAudio}></AudioRecorder>
        )}
        <br></br>
        {asrTranscriptStatus && clickAudioUploadOption ? (
          <div
            style={{
              minHeight: "100px",
              border: "1px solid rgba(128, 128, 128, 0.25)",
            }}
          >
            {" "}
            {asrTranscript}
          </div>
        ) : (
          <></>
        )}
      </Container>
    </>
  );
};

export default ASR;
