import React from "react";
import { useState } from "react";
import axios from "axios";
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";

const OCR = () => {
  const [highlights, setHighlights] = useState([]);
  const [query, setQuery] = useState("");

  const onSubmitHandler = (e) => {
    e.preventDefault();
    console.log("Query:", query);
    let response;
    const getTexts = async () => {
      try {
        /*response = await axios.post("http://localhost:5000/api", {
          query: query,
        });*/

        response = await axios.get(
          "granthapp/api/searchingMongoDBAtlasIndex/" + query
        );

        console.log(response.data["searchRes"]);
      } catch (error) {
        console.log(error);
      }

      if (response.data.length !== 0) {
        setHighlights(response.data["searchRes"]);
      } else {
        setHighlights([]);
      }
    };
    getTexts();
  };

  const printResults = () => {
    if (highlights.length === 0) {
      return <p>   </p>;
    } else {
      return (
        <table className="table ">
          <thead className="thead-light">
            <tr>
              <th> # </th>
              {/*<th>  (Score) </th>*/}
              <th>  (Result)</th>
              <th>  (Source)</th>
              {/* <th>  (Number)</th> */}
            </tr>
          </thead>
          <tbody>
            {highlights
              .sort((a, b) => b.score - a.score)
              .map((item, index) => {
                return (
                  <tr>
                    <td>{index + 1}</td>
                    {/*<td>{item.score}</td>*/}
                    <td key={index}>
                      {item["resHighlights"][0]["texts"].map((text, index1) => {
                        if (text.type === "hit") {
                          return (
                            <span
                              key={index1}
                              style={{
                                fontWeight: "bold",
                              }}
                            >
                              {text.value}{" "}
                            </span>
                          );
                        } else {
                          return text.value;
                        }
                      })}
                    </td>
                    <td>{item.name}</td>
                    {/* <td>{item.line}</td> */}
                  </tr>
                );
              })}
          </tbody>
        </table>
      );
    }
  };

  return (
    <>
      <Container className="mt-5">
        <Form onSubmit={onSubmitHandler}>
          <Form.Group className="mb-3">
            <Form.Control
              type="input"
              placeholder=" (Query)"
              onChange={(e) => setQuery(e.target.value)}
            />
          </Form.Group>
          <Button
            variant="outline-success"
            type="submit"
            style={{ backgroundColor: "rgb(239, 231, 214)" }}
          >
             (Search)
          </Button>
        </Form>
      </Container>
      {highlights.length !== 0 && (
        <Container>
          <p>&nbsp;</p>
          Search Results: {highlights.length}
        </Container>
      )}
      <Container className="mt-3">{printResults()}</Container>
    </>
  );
};

export default OCR;
